/* Title and version */
.app-title {
  font-size: 1.85rem;
  font-weight: 800;
  margin: 0;
}
.version-badge {
  display: inline-block;
  margin-top: 6px;
  padding: 4px 8px;
  border-radius: 8px;
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  font-size: .9rem;
  color: #374151;
}

/* Buttons used in toolbar */
.btn {
  border: 1px solid #d1d5db;
  background: #ffffff;
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}
.btn:hover { background: #f9fafb; }

/* Legend squares */
.legend-yellow { background-color: #fef3c7; } /* soft yellow */
.legend-red    { background-color: #fecaca; } /* soft red */
.legend-blue   { background-color: #dbeafe; } /* soft blue */

/* Stats pills */
.stat-chip { background: #f3f4f6; padding: 4px 8px; border-radius: 6px; }

/* Purple underline for likely hard words */
.editor-content span[data-hard-word="1"] {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-color: #7c3aed;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
}

/* Editor sentence highlights, stable across themes */
.editor-content mark[data-highlight-color] {
  color: inherit;
  padding: 0 .15em;
  border-radius: 4px;
}
.editor-content mark[data-highlight-color="yellow"] { background-color: #fef3c7; }
.editor-content mark[data-highlight-color="red"]    { background-color: #fecaca; }
.editor-content mark[data-highlight-color="blue"]   { background-color: #dbeafe; }

/* Help accordion you had earlier, left as is */
.help-card { border: 1px solid #e5e7eb; border-radius: 8px; background: #ffffff; overflow: hidden; }
.help-summary { cursor: pointer; padding: 12px 14px; font-weight: 700; list-style: none; background: #f9fafb; }
.help-card[open] .help-summary { border-bottom: 1px solid #e5e7eb; }
.help-body { padding: 14px; }
.help-section + .help-section { margin-top: 16px; }
.help-section h3 { font-size: 1.05rem; font-weight: 700; margin: 0 0 6px; }
.help-pre {
  background: #f3f4f6; border: 1px solid #e5e7eb; border-radius: 6px;
  padding: 10px; white-space: pre-wrap;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.92rem; line-height: 1.35; margin: 8px 0;
}
.help-actions { margin: 6px 0 2px; }
.help-note { color: #6b7280; font-size: 0.9rem; margin-top: 4px; }
.help-list { list-style: disc; padding-left: 1.25rem; margin: 0; }
.help-list li + li { margin-top: 4px; }
.legend-underline-purple { text-decoration: underline dotted #7c3aed; text-underline-offset: 2px; font-weight: 700; }
